/*
 ** Sphere subcomponent
 ** Shows a fluid loader
 ** eg: <Loader style={styles} />
 ** styles = {
 **     display: "block",
        ...
 **   };
 */

export default function Loader({ style }) {
  return (
    <div style={style} className="preloader">
      <div className="inner">
        <span className="logo">S</span>
        <span className="thinking">Sebastien is pulling</span>
        <div className="progress-bar">
          <div className="progress-bar_bg">
            <div className=""></div>
          </div>
        </div>
      </div>
    </div>
  );
}
