import React, { Component } from "react";
import TagCloud from "react-tag-cloud";
import Letter from "./subcomponents/BounceLetter";
import Sphere from "./subcomponents/Sphere";
import "../styles/Skills.css";

const skills = [
  { id: 1, href: "#mysql_", title: "MySQL", color: "" },
  { id: 2, href: "#sql_", title: "SQL", color: "" },
  { id: 3, href: "#js_", title: "JavaScript", color: "golden" },
  { id: 4, href: "#php_", title: "PHP", color: "" },
  { id: 5, href: "#java_", title: "Java", color: "" },
  { id: 6, href: "#jquery_", title: "jQuery", color: "golden" },
  { id: 7, href: "#drupal_", title: "Drupal", color: "" },
  { id: 8, href: "#html_", title: "HTML", color: "golden" },
  { id: 9, href: "#wordpress_", title: "Wordpress", color: "golden" },
  { id: 10, href: "#css_", title: "CSS", color: "golden" },
  { id: 11, href: "#json_", title: "JSON", color: "" },
  { id: 12, href: "#git_", title: "Git", color: "" },
  { id: 13, href: "#symfony_", title: "Symfony", color: "" },
  { id: 14, href: "#jommla_", title: "Joomla", color: "" },
  { id: 15, href: "#XML_", title: "XML", color: "" },
  { id: 16, href: "#postgre_", title: "Postgre", color: "" },
  { id: 17, href: "#dp_", title: "DP", color: "golden" },
  { id: 18, href: "#oop_", title: "OOP", color: "golden" },
  { id: 19, href: "#reactjs_", title: "ReactJS", color: "golden" },
  { id: 20, href: "#dom_", title: "Dom Manipulation", color: "golden" },
  { id: 21, href: "#backbone_", title: "Backbone", color: "golden" },
  { id: 22, href: "#wxmlwxss_", title: "WXML/WXSS", color: "" },
  { id: 23, href: "#bootstrap_", title: "Boostrap", color: "golden" },
  { id: 24, href: "#cke_", title: "CKEditor", color: "" },
  { id: 25, href: "#handlebar_", title: "Handlebars", color: "golden" },
  { id: 26, href: "#CSP_", title: "CSP compliance", color: "golden" }
];

export default class Skills extends Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 0,
      shownGraph: { name: "Sphere", type: "cloud" }
    };
  }
  updateGraph = (e) => {
    e.preventDefault();

    if (this.state.shownGraph.type === "cloud") {
      this.setState({ shownGraph: { name: "Cloud", type: "sphere" } });
    } else {
      this.setState({ shownGraph: { name: "Sphere", type: "cloud" } });
    }
  };

  componentDidMount() {
    var count = 0;
    this.intervalID = setInterval(() => {
      this.forceUpdate();
      count = count + 1;
      if (count === 1) {
        clearInterval(this.intervalID);
        this.runAnimation = setInterval(() => {
          this.forceUpdate();
        }, 2000);
      }
    }, 500);
  }
  componentWillUnmount() {
    clearInterval(this.runAnimation);
  }
  render() {
    return (
      <div>
        <span className="tags top-tags"> &nbsp;&nbsp;&nbsp;&lt;body&gt;</span>
        <div className="text-zone">
          <h1 className="golden">
            <Letter letterInfo="Skills&nbsp;&&nbsp;Experience" />
          </h1>

          <p>
            I started to try to modify the things on my computer since i was
            young, with, for first achievement with a blog made by HTML/CSS
            (2001).
          </p>
          <p>
            I have a strong work experience of over 10 years in the web
            development industry, with teams of different size, in office and
            remote.
          </p>
          <p>
            My main area of expertise is front end development and everything
            related with this side of the web. I've extensively worked on
            Javascript, HTML, CSS, Responsive Web Design, security, scalability
            and design.
          </p>
          <p>
            I also have some experience with ReactJS, CMSs, Miniprograms,
            Handlebars, Bootstrap, PHP, Java.
          </p>
        </div>
        <span className="tags bottom-tags">
          {" "}
          &nbsp;&nbsp;&nbsp;&lt;/body&gt;
          <br /> &lt;/html&gt;{" "}
        </span>

        <div className="cloud">
          <button onClick={this.updateGraph} className="skillsGraphButton">
            {this.state.shownGraph.name}
          </button>

          {this.state.shownGraph.type === "sphere" ? (
            <Sphere skills={skills} />
          ) : null}
          {this.state.shownGraph.type === "cloud" ? (
            <TagCloud
              className="tag-cloud cloudHelper"
              style={{
                fontFamily: "font-file-82132",
                fontSize: 28,
                color: "#08fdd8",
                padding: 5,
                width: "50%",
                height: "100%"
              }}
            >
              {skills.map((skill) => {
                return (
                  <div key={skill.id} className={skill.color}>
                    {skill.title}
                  </div>
                );
              })}
            </TagCloud>
          ) : null}
        </div>
      </div>
    );
  }
}
