import React, { useRef } from "react";
import emailjs from "emailjs-com";
import Letter from "./subcomponents/BounceLetter";
import FormElements from "./subcomponents/FormElements";
import Map from "./subcomponents/Maps.js";
import "../styles/ContactMe.css";

export default function ContactMe() {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_wt28jhe",
        "template_qc0sv06",
        form.current,
        "W-XUgM5a_dQvd-do4"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
    e.target.reset();
  };

  return (
    <div>
      <span className="tags top-tags"> &nbsp;&nbsp;&nbsp;&lt;body&gt;</span>
      <div className="text-zone">
        <h1 className="golden">
          <Letter letterInfo="Contact&nbsp;me" />
        </h1>

        <p>Currently interested in opportunities in Bangkok.</p>
        <p>If you have any questions or requests, feel free to contact me.</p>

        <div className="contact-form">
          {
            <form id="contact" ref={form} onSubmit={sendEmail}>
              <FormElements />
            </form>
          }
        </div>
      </div>

      <Map></Map>

      <span className="tags bottom-tags">
        {" "}
        &nbsp;&nbsp;&nbsp;&lt;/body&gt;
        <br /> &lt;/html&gt;{" "}
      </span>
    </div>
  );
}
