/*
 ** Sphere subcomponent
 ** Take a table a skills and renders it in a pivotating sphere.
 ** eg: <Sphere skills={skills} />
 ** skills = [
 **  { id: 1, href: "#firstHref", title: "firstSkillTitle", color: "white" },
 **  { id: 2, href: "#secondHref", title: "secondSkillTitle", color: "" },
 **  ...
 **  ]
 */

import $ from "jquery";
import { useEffect } from "react";
import TagCanvas from "jquery-tagcanvas";

export default function Sphere({ skills }) {
  const canvasOptions = {
    textColour: "#d4af37",
    outlineThickness: 0.5,
    outlineColour: "#FE0853",
    maxSpeed: 0.06,
    freezeActive: true,
    shuffleTags: true,
    shape: "sphere",
    zoom: 0.8,
    wheelZoom: false,
    noSelect: true,
    textFont: null,
    freezeDecel: true,
    fadeIn: 3000,
    initial: [0.3, -0.1],
    depth: 1.1
  };

  const Skills = ({ ...rest }) => {
    useEffect(() => {
      try {
        eval(
          `try {
            TagCanvas.Start(
               'myCanvas',
               '',
               canvasOptions
             );
           }
           catch(e) {
             console.log("error");
             document.getElementById('myCanvasContainer').style.display = 'block';
           }`
        );
      } catch (e) {
        console.log("Canvas error.");
        console.log(e);
      }
    }, []);
  };

  Skills(skills);

  return (
    <div className="SphereContainer">
      <div className="skills-charts">
        <div id="myCanvasContainer">
          <canvas width="600" height="800" id="myCanvas">
            <ul>
              {skills.map((skill) => {
                return (
                  <li key={skill.id}>
                    <a
                      data-weight="25"
                      href={skill.href}
                      target="_blank"
                      rel="noreferrer"
                    >
                      {skill.title}
                    </a>
                  </li>
                );
              })}
            </ul>
          </canvas>
        </div>

        <div className="clear"></div>
      </div>
    </div>
  );
}
