import React, { Component } from "react";
import Loader from "./components/subcomponents/Loader";
import NavBar from "./components/NavBar";
import Home from "./components/Home";
import AboutMe from "./components/AboutMe";
import Skills from "./components/Skills";
import MyWork from "./components/MyWork";
import ContactMe from "./components/ContactMe";

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      listDataFromChild: "",
      isLoaded: false,
      items: ""
    };
  }

  getData = (dataFromChild) => {
    this.setState({
      displayState: "block"
    });
    var self = this;
    setTimeout(
      function () {
        this.setState({ displayState: "none" });
        self.setState({ listDataFromChild: dataFromChild });
      }.bind(this),
      550
    );
  };

  componentDidMount() {
    this.setState({
      isLoaded: true,
      data: { bio: "Front End Developer - Software engineer" }
    });
  }
  render() {
    var styles = {
      display: "block"
    };
    var hidePreloader = {
      display: this.state.displayState
    };
    const { error, isLoaded, data } = this.state;
    if (error) {
      return <div className="error">Error: {error.message}</div>;
    } else if (!isLoaded) {
      return <Loader style={styles} />;
    } else {
      return (
        <div className="App unselectable">
          <NavBar activePage={this.getData} />

          <div id="page">
            <div
              className={`container home-page ${
                this.state.listDataFromChild.dataFromChild === "Home"
                  ? "home"
                  : ""
              } ${
                this.state.listDataFromChild.dataFromChild === "Contact"
                  ? "contact-page"
                  : ""
              } ${
                this.state.listDataFromChild.dataFromChild === "Work"
                  ? "work-page"
                  : ""
              } ${
                this.state.listDataFromChild.dataFromChild === "About"
                  ? "about-page"
                  : ""
              }`}
            >
              {this.state.listDataFromChild ? null : (
                <div className="showPage">
                  <Home activePage={this.getData} jsonData={data} />
                  <div className="bg">S</div>
                </div>
              )}
              {this.state.listDataFromChild.dataFromChild === "Home" ? (
                <div className="showPage">
                  <Home activePage={this.getData} jsonData={data} />
                  <div className="bg">S</div>
                </div>
              ) : null}
              {this.state.listDataFromChild.dataFromChild === "About" ? (
                <div className="showPage about">
                  <AboutMe />
                </div>
              ) : null}
              {this.state.listDataFromChild.dataFromChild === "Skills" ? (
                <div className="showPage skills">
                  <Skills />
                </div>
              ) : null}
              {this.state.listDataFromChild.dataFromChild === "Work" ? (
                <div className="showPage work">
                  <MyWork />
                </div>
              ) : null}
              {this.state.listDataFromChild.dataFromChild === "Contact" ? (
                <div className="showPage contact">
                  <ContactMe jsonData={data} />
                </div>
              ) : null}
            </div>

            <Loader style={hidePreloader} />
          </div>
        </div>
      );
    }
  }
}
