import React, { Component } from "react";
import Letter from "./subcomponents/BounceLetter";
import "../styles/Home.css";

class Home extends Component {
  constructor(props) {
    super(props);
    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick = (event) => {
    var menu_name = String(event.currentTarget.dataset.menu);
    this.props.activePage({ dataFromChild: menu_name });
  };
  render() {
    return (
      <div>
        <span className="tags top-tags"> &nbsp;&nbsp;&nbsp;&lt;body&gt;</span>
        <div className="text-zone">
          <h1 className="golden">
            <Letter letterInfo="Hey&nbsp;there," />
            <br />
            <Letter letterInfo="I'm&nbsp;" />
            <a className="logo" rel="index" href="http://localhost:3000/">
              {" "}
              S
            </a>
            <Letter letterInfo="ebastien." />
            <br />
          </h1>

          <br />
          <h2>{this.props.jsonData.bio}</h2>
          <a
            rel="contact"
            data-menu="Contact"
            onClick={this.handleClick}
            className="flat-button"
          >
            CONTACT ME
          </a>
        </div>
        <span className="tags bottom-tags">
          {" "}
          &nbsp;&nbsp;&nbsp;&lt;/body&gt;
          <br /> &lt;/html&gt;{" "}
        </span>
      </div>
    );
  }
}

export default Home;
