/*
 ** Bounce letter subcomponent
 ** Takes a string and renders it as bouncy letters on hover.
 ** eg: <Letter letterInfo="Hello" />
 */

import React, { useState } from "react";

export default function Letter({ letterInfo }) {
  const makeTable = () => {
    const theTable = [];
    const theLetters = [...letterInfo];

    for (var i = 0; i < theLetters.length; i++) {
      theTable[i] =
        theLetters[i] !== " "
          ? { id: i + 1, value: theLetters[i], className: "blast" }
          : { id: i + 1, value: " ", className: "blast letterSpace" };
    }
    return theTable;
  };

  const [hoverLetters, setHoverLetters] = useState(makeTable());

  const toggleHoverout = (id) => {
    const lettersCopy = [...hoverLetters];

    const letterCopyUpdated = lettersCopy.filter((letter) => {
      if (letter.id === id) {
        letter.className =
          letter.className.indexOf("letterSpace") > -1
            ? "blast letterSpace"
            : "blast";
      }
      return true;
    });

    setHoverLetters(letterCopyUpdated);
  };
  const toggleHover = (id) => {
    const lettersCopy = [...hoverLetters];

    const letterCopyUpdated = lettersCopy.filter((letter) => {
      if (letter.id === id) {
        letter.className =
          letter.className.indexOf("letterSpace") > -1
            ? "blast animated rubberBand letterSpace"
            : "blast animated rubberBand";

        setTimeout(function () {
          toggleHoverout(id);
        }, 800);
      }
      return true;
    });

    setHoverLetters(letterCopyUpdated);
  };

  return (
    <div className="floatLeft">
      {hoverLetters.map((letter) => {
        return (
          <span
            key={letter.id}
            className={letter.className}
            onMouseEnter={() => {
              toggleHover(letter.id);
            }}
          >
            {letter.value}
          </span>
        );
      })}
    </div>
  );
}
